import React from 'react';
import style from './Post.module.css';

const Post = ({message, likes}) => {


  return (
    <div className={style.item}>
      <img src='https://user-life.com/uploads/posts/2018-08/1535602801_kak-delat-avatarki-krasivye-prikolnye-krutye-dlya-devushek-besplatno-foto-kartinki-dlya-vk-stima-yutuba-instagram-vatsapa.jpg'></img>
         {message} 
      <div className={style.like}>
        <span>like</span> {likes}
      </div>
    </div>
  )
}

export default Post;