import React, { useState } from 'react';
import Preloader from '../../common/Preloader/Preloader';
import style from './ProfileInfo.module.css';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';
import noPhotoUser from '../../../assets/images/user.png';
import ProfileData from './ProfileData';
import ProfileDataEditForm from '../../common/Forms/profileDataEditForm';



const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto, saveProfile}) => {

    let [editMode, setEditModeActive] = useState(false)

    if(!profile){
        return <Preloader />
    }

    const onMainPhotoSelected = (e) => {
        if(e.target.files.length){
            savePhoto(e.target.files[0])
        }
    }

    const onSubmit = (formData) => {
        saveProfile(formData)
        setEditModeActive(false)
    }


    return (
        <div>
           {/*  <div>
                <img src='https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTU1dFR0DR4d0wILeQRaH1oo1Dlsag-Gp2dXg&usqp=CAU'></img>
            </div> */}
            <div className={style.descriptionBlock}>
                <img src={profile.photos.large || noPhotoUser} className={style.mainPhoto}/>
                {isOwner && <input type={"file"} onChange={onMainPhotoSelected}/>}
            </div>
            <ProfileStatusWithHooks status={status} 
                                    updateStatus={updateStatus}/>
            {!editMode ?
            <ProfileData profile={profile} isOwner={isOwner} goToEditMode={() => {setEditModeActive(true)}} editMode={false}/>
            : <ProfileDataEditForm initialValues={profile} profile={profile} subscription={{ submitting: true, pristine: true}} onSubmit={onSubmit} editMode={true}/>
}
        </div>
    )
}



export default ProfileInfo;