import React from 'react';
import { Field } from 'react-final-form';
import facebook from '../../../assets/images/Social_Icons_v3_(graphicsland.ru)/png 500px/Facebook.png'
import { FieldArray } from 'react-final-form-arrays'
import { composeValidators, linkValidator } from '../../../utils/validators/validators';
import RenderCount from '../Forms/renderCount';

const SocialContact = ({profile, editMode, contactTitle, contactValue, validate, initialValues }) => {

    if (!editMode) {
        return (
            <div>{contactTitle} : {contactValue}</div>
        )
    } else {
        const contacts = Object.keys(profile.contacts).map(key => {
            return key
        })

        
    
        
        return (
            <FieldArray name="contacts" fields={contacts}>
                {({ fields, subscription }) => (
                    <div>
                        {fields.map((name, index) => (
                            <div key={name}>
                                <div>
                                    <label>{name}</label>
                                    <Field name={`contacts.${name}`} component="input" validate={validate} subscription={subscription} index={index}>
                                        {({ input, meta }) => (
                                            <div>
                                                     <RenderCount />
                                                <input {...input} placeholder={name} subscription={subscription}/>
                                                {meta.error && meta.touched && <span>{meta.error}</span>}
                                            </div>
                                        )}
                                    </Field>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </FieldArray>
        )
    }
}


const SocialContacts = (props) => {
    //console.log(props)
    return <div><SocialContact {...props} /></div>
}

export default SocialContacts;