import React from 'react';
import { Field, Form } from 'react-final-form';
import {  composeValidators, maxLengthCreator, required } from '../../../utils/validators/validators';
import { Textarea } from '../../common/Forms/formsControl';
import style from './MyPosts.module.css';
import Post from './Post/Post';

let maxLength15 = maxLengthCreator(15);

const MyNewPost = (props) => (
  <Form
    onSubmit={props.onSubmit}
    subscription={props.subscription}
    validate={props.validate}
    render={({ handleSubmit,
               form,
               submitting,
               pristine,
               values
             }) => (
      <form onSubmit={handleSubmit}>
        <div>
        <Field 
        name="myNewPost" 
        component={Textarea}
        validate={props.validate} />
        </div>
            <div>
              <button disabled={submitting || pristine }>Add post</button>
            </div>
      </form>
    )}
  />
)

const MyPosts = React.memo(({postsData, addPost, ...props}) => {
  let postsElements = postsData.map((p, index=p.id) => <Post message={p.message} likes={p.likesCount} key={index}/>);
  

  const onAddPost = (myNewPost) => {
    addPost(myNewPost);
  }

  const onSubmit = (values) => {
    onAddPost(values.myNewPost)
}

  return (
    <div className={style.postsBlock}>
      <h3>My posts</h3>
      <div>
        <MyNewPost subscription={{ submitting: true, pristine: true}} onSubmit={onSubmit} validate={ composeValidators(maxLength15, required) } props ={props} />
      </div>
      <div className={style.posts}>
        {postsElements}
      </div>
    </div>
  )
});

export default MyPosts;