import React from 'react';
import jobStatusOn from '../../../assets/images/red-v.png';
import jobStatusOff from '../../../assets/images/red-x.png';
import SocialContacts from '../../common/SocialNetworck/SocialNetwork';

const ProfileData = ({ profile, isOwner, goToEditMode, editMode }) => {
    const lookingStatus = (profile.lookingForAJob === true ? jobStatusOn : jobStatusOff);
    return (
        <div>
            <div>
                <b>Full name</b>: {profile.fullName}
            </div>
            <div>
                <b>Looking for a job</b>: <img src={lookingStatus} />
            </div>
            <div>
                <b>My professional skills</b>: {profile.lookingForAJobDescription}
            </div>
            <div>
                <b>About me</b>: {profile.aboutMe}
            </div>
            <div><b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
                return <SocialContacts key={key} contactTitle={key} contactValue={profile.contacts[key]} editMode={editMode} profile={profile}/>
            })}

            </div>
            {isOwner && <div><button onClick={goToEditMode}>Edit</button></div>}
        </div>
    )
}

export default ProfileData